import {TinyEmitter} from 'tiny-emitter';

const emitter = new TinyEmitter();

export function on(msg: string, func: Function) {
    emitter.on(msg, func);
}

export function off(msg: string, func: Function) {
    emitter.off(msg, func);
}

export function emit(msg: string, data: any) {
    emitter.emit(msg, data);
}

export const MsgLookRepeat = 'look-repeat';