
import {defineComponent, onMounted, ref} from 'vue';
import TButton from '@/rich-text-editor/comp/btn.vue';
import Panel from '@/core/Panel';
import {TextureData} from './type';
import TextureInfo from '@/cache-info/texture.vue';
import CcCheckbox from '@/packages/cc-checkbox/checkbox.vue';
import {emit, MsgLookRepeat} from '@/cache-info/util';
import {forEach} from 'lodash';

const EditorPanel = 'editor';


export default defineComponent({
  name: 'app',
  components: { CcCheckbox, TextureInfo, TButton },
  setup(props) {
    const count = ref(0);
    const textures = ref<Array<TextureData>>([]);
    const panel = new Panel('cache-info');

    function updateList() {
      panel.dispatch({
        panel: EditorPanel,
        action: 'get-texture-cache-info-list',
        response: (data: any) => {
          const infos: Record<string, TextureData> = data.data as Record<string, TextureData>;
          textures.value = [];
          const arr = [];
          // infos['1'] = { path: '111', url: '', width: 0, height: 0, key: '', repeat: false };
          // infos['12'] = { path: '111', url: '', width: 0, height: 0, key: '', repeat: false };
          const repeatMap: Record<string, TextureData[]> = {};
          for (let key in infos) {
            const item = infos[key];

            const newItem = {
              key: key,
              repeat: false,
              url: item.url,
              path: item.path,
              width: item.width,
              height: item.height,
            };
            arr.push(newItem);

            const { width, height, path, url } = item;
            if (path) {
              if (repeatMap[path] === undefined) {
                repeatMap[path] = [];
              }
              repeatMap[path].push(newItem);
            }
          }
          for (let key in repeatMap) {
            const items = repeatMap[key];
            if (items.length > 1) {
              for (let i = 0; i < items.length; i++) {
                repeatMap[key][i].repeat = true;
              }
            }
          }
          arr.sort((a: TextureData, b: TextureData) => {
            const min = Math.min(a.key.length, b.key.length);
            let n = 0;
            while (n < min) {
              const char1 = a.key.charCodeAt(n);
              const char2 = b.key.charCodeAt(n);
              if (char1 !== char2) {
                return char1 - char2;
              }
              n++;
            }
            return 0;
          });
          textures.value = arr;
          count.value = arr.length || 0;
        },
      });
    }

    onMounted(async () => {
      await panel.init();
      updateList();
    });
    return {
      count,
      textures,
      onClickFresh() {
        updateList();
      },
      onChangeLook(v: boolean) {
        emit(MsgLookRepeat, v);
      },
    };
  },
});
